import React, { useState, useEffect } from 'react'
import { NavLink } from "react-router-dom";

const NavComponent = ({ heading, imageURL }) => {
    const [values, setValues] = useState({
        Name: '',
        Email: '',
        Phone: '',
        Message: '',
        formData: ''
    });


    const FORMURL = 'https://script.google.com/macros/s/AKfycbxmZCmzGXYGM2bBPdMxo7Hwsox3sIpXNt-HfFWvsc8qJRaIPQc/exec'

    const {
        Name,
        Email,
        Phone,
        Message,
        formData,
    } = values;

    useEffect(() => {
        setValues({ ...values, formData: new FormData() });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);


    const handleChange = (name) => (e) => {
        const value = e.target.value;
        setValues({ ...values, [name]: value });
        formData.set(name, value);
    };

    const submitHandler = (e) => {
        e.preventDefault();
        console.log('ready to submit information');
        fetch(FORMURL, { method: 'POST', body: formData })
            .then((response) =>
                alert('Thanks for Contacting us..! We Will Contact You Soon...')
            )
            .catch((error) => console.error('Error!', error.message));
    }
    return (
        <>
            <div className="contact-overlay">
                <div className="w-container">
                    <a className="close-link" data-ix="close-contact-overlay" href="/#" style={{ cursor: "pointer" }}>CLOSE&nbsp;✕</a>
                    <div className="form-wrapper">
                        <div className="contact-heading">Get in Touch</div>
                        <div className="small-divider" />
                        <div className="contact-text">
                            Thank you for your interest! Please fill out the form below if you
                            would like to get in touch.
                        </div>
                        <div className="w-form">
                            <form className="gform" method="post" autoComplete="off" name="google-sheet" onSubmit={submitHandler}>
                                <input type="text" name="Name" className="text-field w-input" placeholder="Enter Your Name" value={Name} required onChange={handleChange('Name')} />
                                <input type="text" name="Email" className="text-field w-input" placeholder="Enter your email address" value={Email} required onChange={handleChange('Email')} />
                                <input type="text" name="Phone" className="text-field w-input" placeholder="Your Contact Number" value={Phone} required onChange={handleChange('Phone')} />
                                <textarea className="text-area text-field w-input" required maxLength={5000} name="Message" type="text" placeholder="Your message. Please mention the subject in the beginning..." value={Message} onChange={handleChange('Message')} />

                                <button type="submit" className="submit-button w-button">
                                    Submit
                                </button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            <div className="hero-section home-page-hero" style={{ backgroundImage: `url(assets/images/${imageURL})` }}>
                <div className="nav w-nav" data-animation="default" data-collapse="medium" data-contain={1} data-duration={400}>
                    <div className="w-container">
                        <a className="w-nav-brand w--current" href="/">
                            <div className="logo-text">
                                EMON<strong data-new-link="true">HOSSAIN</strong>
                            </div>
                        </a>
                        <nav className="nav-menu w-nav-menu w-preserve-3d" role="navigation">
                            <NavLink className="nav-link w-nav-link" to="/" style={{ maxWidth: '940px' }}>Home</NavLink>
                            <NavLink className="nav-link w-nav-link" activeClassName="current w--current" to="/about" style={{ maxWidth: '940px' }}>About</NavLink>
                            <NavLink className="nav-link w-nav-link" activeClassName="current w--current" to="/creativity-corner" style={{ maxWidth: '940px' }}>Blog</NavLink>

                            <a className="contact nav-link w-nav-link" data-ix="show-contact-overlay" href="/#/" style={{ maxWidth: '940px' }}>Contact</a>
                        </nav>
                        <div className="menu-button w-nav-button">
                            <div className="w-icon-nav-menu" />
                        </div>
                    </div>
                    <div className="w-nav-overlay" data-wf-ignore />
                </div>
                <h1 className="home-page-heading" data-ix="hero-text" style={{ opacity: 1, transform: 'translateX(0px) translateY(0px) translateZ(0px)', transition: 'opacity 700ms ease 0s, transform 700ms ease 0s' }}>
                    {heading}
                </h1>
                <div className="hero-overlay" />
            </div>
        </>
    )
}

export default NavComponent
