import React from 'react'
import Nav from './navComponent'

export default function creativity() {
    return (
        <>
            <Nav heading={'Creativity Corner'} imageURL={'design-desk.jpg'} />
            <div className="hero-container w-container">
                <div className="w-clearfix">
                    <div className="_60block" data-ix="tile-3" style={{ opacity: 1, transform: 'scaleX(1) scaleY(1) scaleZ(1)', transition: 'opacity 700ms ease 0s, transform 700ms ease 0s' }}>
                        <div className="hero-tile w-dyn-list">
                            <div className="w-dyn-items">
                                <div className="w-dyn-item">
                                    {/* /creativity-corner/technical_blogs */}
                                    <a className="tile-link w-inline-block" href="https://emonhossainraihan.medium.com/">
                                        <div className="tile-text">TECHNICAL BLOGS.</div>
                                        <div className="tile-image" style={{ backgroundImage: 'url("../assets/images/tile_blog_technical.jpg")' }}>
                                            <div className="tile-overlay" />
                                        </div>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="_40block" data-ix="tile-3" style={{ opacity: 1, transform: 'scaleX(1) scaleY(1) scaleZ(1)', transition: 'opacity 700ms ease 0s, transform 700ms ease 0s' }}>
                        <div className="hero-tile w-dyn-list">
                            <div className="w-dyn-items">
                                <div className="w-dyn-item">
                                    {/* /creativity-corner/personal_blogs */}
                                    <a className="tile-link w-inline-block" href="#/">
                                        <div className="tile-text">PERSONAL BLOGS.</div>
                                        <div className="tile-image" style={{ backgroundImage: 'url("../assets/images/tile_blog_personal.jpg")' }}>
                                            <div className="tile-overlay" />
                                        </div>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="_100block" data-ix="tile-3" style={{ height: '400px', opacity: 1, transform: 'scaleX(1) scaleY(1) scaleZ(1)', transition: 'opacity 700ms ease 0s, transform 700ms ease 0s' }}>
                        <div className="hero-tile w-dyn-list">
                            <div className="w-dyn-items">
                                <div className="w-dyn-item">
                                    {/* href="/creativity-corner/mathematics" */}
                                    <a className="tile-link w-inline-block" href="#/">
                                        <div className="tile-text">MAKING LIFE MATHEMATICAL.</div>
                                        <div className="tile-image" style={{ backgroundImage: 'url("../assets/images/cover_mathematics.jpg")' }}>
                                            <div className="tile-overlay" />
                                        </div>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
