import React from 'react'
import { Route, Switch } from "react-router-dom";


import Home from './components/homeComponent'
import About from './components/aboutComponent'
import Courses from './components/courses-skills.component'
import Experience from './components/experience.component'
import Project from './components/project.component'
import Community from './components/community.component'
import Accomplishments from './components/accomplishments.component'
import Creativity from './components/creativity.component.jsx'
import PersonalBlog from './components/blog/personal.component'
import TechnicalBlog from './components/blog/technical.component'
import MathematicsBlog from './components/blog/mathematics.component'
import Footer from './components/footerComponent'

export default function mainRoute() {
    return (
        <div>
            <Switch>
                <Route exact path="/courses-skills" component={Courses} />
                <Route exact path="/experience" component={Experience} />
                <Route exact path="/projects" component={Project} />
                <Route exact path="/creativity-corner" component={Creativity} />
                <Route exact path="/accomplishments" component={Accomplishments} />
                <Route exact path="/community-outreach" component={Community} />
                <Route exact path="/about" component={About} />
                <Route exact path='/creativity-corner/personal_blogs' component={PersonalBlog} />
                <Route exact path='/creativity-corner/technical_blogs' component={TechnicalBlog} />
                <Route exact path='/creativity-corner/mathematics' component={MathematicsBlog} />
                <Route exact path="/" component={Home} />
            </Switch>
            <Route component={Footer} />
        </div>
    )
}
