import React from 'react'
import { NavLink } from "react-router-dom";

export default function footerComponent() {
    return (
        <div className="footer-section">
            <div className="w-container">
                <div className="w-row">
                    <div className="w-col w-col-3">
                        <NavLink className="logo-footer w-nav-brand w--current" to="/">
                            <div className="footer-logo logo-text">
                                EMON<strong data-new-link="true">HOSSAIN</strong>
                            </div>
                        </NavLink>
                    </div>
                    <div className="footer-link-col w-clearfix w-col w-col-9">
                        <a target="_blank" rel="noopener noreferrer" className="footer-link nav-link" href="https://medium.com/@emonhossainraihan/"><i className="fab fa-medium" /></a>
                        <a target="_blank" rel="noopener noreferrer" className="footer-link nav-link" href="https://www.instagram.com/emonhossainraihan/"><i className="fab fa-instagram" /></a>
                        <a target="_blank" rel="noopener noreferrer" className="footer-link nav-link" href="https://www.facebook.com/emonhossainraihan/"><i className="fab fa-facebook-square" /></a>
                        <a target="_blank" rel="noopener noreferrer" className="footer-link nav-link" href="https://twitter.com/emonhossain_dev"><i className="fab fa-twitter" /></a>
                        <a target="_blank" rel="noopener noreferrer" className="footer-link nav-link" href="https://github.com/emonhossainraihan"><i className="fab fa-github" /></a>
                        <a target="_blank" rel="noopener noreferrer" className="footer-link nav-link" href="https://www.linkedin.com/in/emonhossainraihan/"><i className="fab fa-linkedin" /></a>
                    </div>
                </div>
            </div>
        </div>
    )
}
