import React from 'react'
import Nav from './navComponent'
import { NavLink } from "react-router-dom";

export default function Home() {
    return (
        <>
            <Nav heading={'I am one of a kind!'} imageURL={'work-late.jpg'} />
            <div className="home-page section top-section">
                <div className="hero-container w-container">
                    <div className="w-clearfix">
                        <div className="_60block" data-ix="tile-1" style={{ opacity: 1, transform: 'scaleX(1) scaleY(1) scaleZ(1)', transition: 'opacity 700ms ease 0s, transform 700ms ease 0s' }}>
                            <NavLink className="hero-tile w-inline-block" to="/about">
                                <div className="tile-text">About<br />Myself.</div>
                                <div className="tile-image" style={{ backgroundImage: 'url("assets/images/about-image.JPG")' }}>
                                    <div className="tile-overlay" />
                                </div>
                            </NavLink>
                        </div>
                        <div className="_40block" data-ix="tile-3" style={{ opacity: 1, transform: 'scaleX(1) scaleY(1) scaleZ(1)', transition: 'opacity 700ms ease 0s, transform 700ms ease 0s' }}>
                            <div className="hero-tile w-dyn-list">
                                <div className="w-dyn-items">
                                    <div className="w-dyn-item">
                                        <a className="tile-link w-inline-block" href="/courses-skills">
                                            <div className="tile-text">Courses and<br />Skills.</div>
                                            <div className="tile-image" style={{ backgroundImage: 'url("assets/images/skills.jpg")' }}>
                                                <div className="tile-overlay" />
                                            </div>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="_33block" data-ix="tile-3" style={{ opacity: 1, transform: 'scaleX(1) scaleY(1) scaleZ(1)', transition: 'opacity 700ms ease 0s, transform 700ms ease 0s' }}>
                            <div className="hero-tile">
                                <div className="w-dyn-list">
                                    <div className="w-dyn-items">
                                        <div className="w-dyn-item">
                                            <NavLink className="tile-link w-inline-block" to="/experience">
                                                <div className="tile-text">Experience.</div>
                                                <div className="tile-image" style={{ backgroundImage: 'url("assets/images/experience.jpg")' }}>
                                                    <div className="tile-overlay" />
                                                </div>
                                            </NavLink>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="_33block" data-ix="tile-2" style={{ opacity: 1, transform: 'scaleX(1) scaleY(1) scaleZ(1)', transition: 'opacity 700ms ease 0s, transform 700ms ease 0s' }}>
                            <NavLink className="hero-tile w-inline-block" to="/projects">
                                <div className="tile-text">Projects.</div>
                                <div className="hire tile-image" style={{ backgroundImage: 'url("assets/images/projects.jpg")' }}>
                                    <div className="tile-overlay" />
                                </div>
                            </NavLink>
                        </div>
                        <div className="_33block" data-ix="tile-2" style={{ opacity: 1, transform: 'scaleX(1) scaleY(1) scaleZ(1)', transition: 'opacity 700ms ease 0s, transform 700ms ease 0s' }}>
                            <NavLink className="hero-tile w-inline-block" to="/creativity-corner">
                                <div className="tile-text">Creativity<br />Corner.</div>
                                <div className="tile-image" style={{ backgroundImage: 'url("assets/images/creativity.jpg")' }}>
                                    <div className="tile-overlay" />
                                </div>
                            </NavLink>
                        </div>
                        <div className="_40block" data-ix="tile-3" style={{ opacity: 1, transform: 'scaleX(1) scaleY(1) scaleZ(1)', transition: 'opacity 700ms ease 0s, transform 700ms ease 0s' }}>
                            <div className="hero-tile w-dyn-list">
                                <div className="w-dyn-items">
                                    <div className="w-dyn-item">
                                        <NavLink className="tile-link w-inline-block" to="#/">
                                            <div className="tile-text">Accomplishments.</div>
                                            <div className="tile-image" style={{ backgroundImage: 'url("assets/images/accomplishment.jpg")' }}>
                                                <div className="tile-overlay" />
                                            </div>
                                        </NavLink>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="w-clearfix">
                            <div className="_60block" data-ix="tile-1" style={{ opacity: 1, transform: 'scaleX(1) scaleY(1) scaleZ(1)', transition: 'opacity 700ms ease 0s, transform 700ms ease 0s' }}>
                                <NavLink className="hero-tile w-inline-block" to="#/">
                                    <div className="tile-text">Community<br />Outreach.</div>
                                    <div className="tile-image" style={{ backgroundImage: 'url("assets/images/community.jpg")' }}>
                                        <div className="tile-overlay" />
                                    </div>
                                </NavLink>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
