import React from 'react'
import Nav from './navComponent'


export default function accomplishments() {
    return (
        <>
            <Nav heading={'Accomplishments'} imageURL={'accomplishment2.jpg'} />
            <div className="section">
                <div style={{ width: '70%', margin: '0 auto' }}>
                    <div style={{ display: 'flex', justifyContent: 'space-between', margin: '40px 0px' }}>
                        <div style={{ minHeight: '250px' }}>
                            <h3>
                                <i className="fas fa-trophy" style={{ marginLeft: '-48px', marginRight: '16px', color: '#c6a612' }} />Kanya Vidya Dhan
              </h3>
                            <div style={{ fontSize: '16px', marginBottom: '6px', marginRight: '10px' }}>
                                Dec 2016
              </div>
                            <div>
                                I was awarded a one-time scholarship of ₹ 30,000 by the Government
                                of Uttar Pradesh for academic excellence.
              </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
